<template>
    <div>
        <div v-if="mode === 'DESIGN'">
            <el-button size="small" icon="el-icon-paperclip" round>选择文件</el-button>
            <ellipsis :row="1" :content="placeholder + sizeTip" hoverTip slot="tip" class="el-upload__tip"/>
        </div>
        <div v-else>
            <el-upload :disabled="disabled" :on-success="uploadSuccess" :file-list="_value"
                       :headers="uploadHeaders" :on-preview="clickFile"
                       :action="uploadUrl" :on-remove="handleRemove"
                       :limit="maxNumber" with-credentials :multiple="maxNumber > 0" :data="uploadParams"
                       :auto-upload="true" :before-upload="beforeUpload">
                <el-button size="small" icon="el-icon-paperclip" round>选择文件</el-button>
                <ellipsis :row="1" :content="placeholder + sizeTip" hoverTip slot="tip" class="el-upload__tip"/>
            </el-upload>
        </div>
    </div>
</template>

<script>
import componentMinxins from '../ComponentMinxins'
import constant from '@/api/constant'

export default {
    mixins: [componentMinxins],
    name: "ImageUpload",
    components: {},
    props: {
        placeholder: {
            type: String,
            default: '请选择附件'
        },
        value: {
            type: Array,
            default: () => {
                return []
            }
        },
        maxSize: {
            type: Number,
            default: 5
        },
        onlyRead: {
            type: Boolean,
            default: false
        },
        maxNumber: {
            type: Number,
            default: 10
        },
        fileTypes: {
            type: Array,
            default: () => {
                return []
            }
        },
        perm: {
            type: String,
            default: 'E'
        }
    },
    mounted() {

        if (this.perm === 'R') {
            this.disabled = true;

        }
    },
    watch: {
        fList: {
            handler(v) {
                console.log(v)
                this.$emit('input', v);
            },
            deep: true
        }
    },
    computed: {
        sizeTip() {
            if (this.fileTypes.length > 0) {
                return ` | 只允许上传[${String(this.fileTypes).replaceAll(",", "、")}]格式的文件，且单个附件不超过${this.maxSize}MB`
            }
            return this.maxSize > 0 ? ` | 单个附件不超过${this.maxSize}MB` : ''
        }
    },
    data() {
        return {
            fList: [],
            uploadHeaders: {
                cxygzl: localStorage.getItem("cxygzl")
            },
            uploadUrl: constant.BASE_URL + "/file/upload",
            disabled: false,
            uploadParams: {}
        }
    },
    methods: {
        beforeUpload(file) {

            var fName = file.name;

            var checkType = false;

            if (this.fileTypes.length > 0) {
                for (var ftype of this.fileTypes) {
                    var reg = new RegExp(ftype + "$", "i")
                    if (reg.test(fName)) {
                        checkType = true;
                        break;
                    }
                }


            }
            if (!checkType) {
                this.$message.warning("文件格式错误")

            } else if (this.maxSize > 0 && file.size / 1024 / 1024 > this.maxSize) {
                this.$message.warning(`单张图片最大不超过 ${this.maxSize}MB`)
            } else {
                return true
            }
            return false
        },
        handleRemove(file, fileList) {

            this.fList = fileList.filter(res => res.uid != file.uid);

        },
        handlePictureCardPreview(file) {
            console.log(file)
        },
        clickFile(file) {
            console.log(file);
            if(!this.onlyRead){
               window.open(file.url)
            }
        },
        uploadSuccess(res, file, fileList) {

            file.url = res;

            this.fList.push(file);
        }
    }
}
</script>

<style lang="less" scoped>

</style>
